import React from 'react';
import Category from './category';
import $ from 'jquery';
import Map from "./multimarker";
import Amountcalc from "./amountcalc";
import Map1 from "./map";
//import places from "./place.json";
const axios = require('axios');

class App1 extends React.Component {
constructor(props)
{
  super(props);
  this.state = {
  isSubmit:false,
  isBack:false,
  isNext:false,
  isClick:false,
  nDocs:0,
  nBox:0,
  DL1:0,
  DL2:0,
  DB1:0,
  DB2:0,
  BL1:0,
  BB1:0,
  BH1:0,
  BL2:0,
  BB2:0,
  BH2:0,
  couriername:[],
  courier:'',
  product:[],
  textboxes:[]
}
}

shouldComponentUpdate( nextProps, nextState ){

  return true;
}

onChange = ( event ) => {
this.setState({ [event.target.name]: event.target.value });
//alert(document.getElementById("prod").value)
};

clickBooking=()=>{
this.setState({ isClick: true });
}

renderRedirect = (type) => {
this.setState({ isSubmit:false });
if(type==='submit') this.setState({ isSubmit:true });
else
{
  if(type==='back') 
  this.setState({ isBack: true,isNext:false });
  else if(type==='next') 
  this.setState({ isBack: false,isNext:true });
}
}

componentDidMount() {

axios.get(global.serverURL+"/common/ProductType/Courier")
.then((response) => {
return response.data;
})
.then(data => {
let teamsFromApi = data.map(team => { return {value: team.ProductType, display: team.ProductType} })
this.setState({ product: teamsFromApi });
}).catch(error => {
console.log(error);
});

axios.get(global.serverURL+"/courier/couriercompany/App")
.then((response) => {
return response.data;
})
.then(data => {
let teamsFromApi = data.map(team => { return {value: team.CourierName, display: team.CourierName} })
this.setState({ couriername: teamsFromApi });
}).catch(error => {
console.log(error);
});

$(function(){
window.$('.select2').select2();
window.$('form.material').materialForm(); // Apply material
window.$('form').validate({ 
errorPlacement: function(error, element) {}
}); // Apply validator with no error messages but classes only
});

};


render()
{
  let res;
  let res1;
  const places1 = [{
  "id": 1,            
  "name": this.props.address,
  "latitude": this.props.fromlat,
  "longitude": this.props.fromlng
},{
"id": 2,
"name": this.props.address1,
"latitude": this.props.tolat,
"longitude": this.props.tolng
}];

let textboxs=[]
let textboxs1=[]
let i=0;
let iname=''
let dl='',db='',dh=''
for(i=0;i<this.state.nDocs;i++)
{
  iname = 'Document '+(i+1)
  dl='DL'+(i+1)
  db='DB'+(i+1)
  textboxs.push( <div className="row" style={{paddingRight:'7px',paddingLeft:'5px'}}>
    <div className="col-12">  <label className="select2text">{iname}</label>  </div>
    <div className="col-4">
      <input type="text" className="form-control" name={dl} placeholder="Length" onChange={ this.onChange } required />
    </div>
    <div className="col-4">
      <input type="text" className="form-control" name={db} placeholder="Breadth" onChange={ this.onChange } required />
    </div>
    <div className="col-4 mb-2">
      <input type="text" className="form-control" name="nDocs" placeholder="Breadth" style={{visibility:'hidden'}} />
    </div>
  </div>);
}

for(i=0;i<this.state.nBox;i++)
{
  iname = 'Box '+(i+1)
  dl='BL'+(i+1)
  db='BB'+(i+1)
  dh='BH'+(i+1)  
  textboxs1.push(
  <div className="row" style={{paddingRight:'7px',paddingLeft:'5px'}}>
    <div className="col-12">  <label className="select2text">{iname}</label>  </div>
    <div className="col-4">
      <input type="text" className="form-control" name={dl} placeholder="Length" onChange={ this.onChange } required />
    </div>
    <div className="col-4">
      <input type="text" className="form-control" name={db} placeholder="Breadth" onChange={ this.onChange } required />
    </div>
    <div className="col-4 mb-2">
      <input type="text" className="form-control" name={dh} placeholder="Height" onChange={ this.onChange } required />
    </div>
  </div>);
}

const isClick1=this.state.isClick;

res = <div className="wrapper">
  <div className="">
    <div className="row">
      <div className="col-md-6 col-xs-12 text-center">

        <div className="card card-success card-outline">
          <div className="card-header">

            <h3 className="card-title text-center">
              <div className="row rowflexdisable">
                <div className="col-md-3 col-xs-6">
                  <button className="btn1 btn-4 btn-4b" style={{float:'left',fontWeight:'500'}} onClick={()=>this.renderRedirect('back')}><i className="fa fa-arrow-left">&nbsp;Back</i></button>
                </div>
                <div className="col-md-6 col-xs-6">
                  <span style={{marginTop:'10px',fontFamily:'fantacy',fontWeight:'500',fontSize:'28px',
                  background: 'linear-gradient(to right, darkviolet 0%, #f4511e 100%)',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',marginLeft:'-10px' }}> Local </span>
                </div>
                <div className="col-md-3 col-xs-6">
                  <button className="btn1 btn-4 btn-4a" style={{float:'right',fontWeight:'500'}} onClick={()=>this.renderRedirect('next')}><i className="fa fa-arrow-right">&nbsp;Next</i></button>
                </div>
              </div>
            </h3>
          </div>

          <div className="card-body">
            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <button type="button" className="btn btn-success" style={{width: "70px"}}>From</button>
              </div>
              <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address }/>
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <button type="button" className="btn btn-danger" style={{width: "70px"}}>To</button>
              </div>
              <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address1 }/>
            </div> 

            <form action="" className="material">

              <div className="form-group">
                <label className="select2text">Product</label>
                <select className="form-control select2" style={{width: '100%'}} placeholder="Product">
                  <option key="select">Select Product</option>
                  {this.state.product.map((team) => <option key={team.value} value={team.value}>{team.display}</option>)}
                </select>
              </div>

              <input type="text" name="receivername" placeholder="Receiver Name" required />
              <input type="text" name="receiveradd1" placeholder="Receiver Address1" required />
              <input type="text" name="receiveradd2" placeholder="Receiver Address2" />
              <input type="text" name="receivermobno" placeholder="Receiver MobileNo" required />

              <div className="form-group">
                <label className="select2text">Number of Items</label>
                <select className="form-control" style={{width: '100%'}} placeholder="Number of Items">
                  <option key="select">Select No. of Items</option>
                  <option key="1">1</option>
                  <option key="2">2</option>
                  <option key="3">3</option>
                </select>
              </div>

              <textarea name="remarks" placeholder="Remarks"></textarea>

            </form>

          </div>
          <div className="row rowflexdisable" style={{paddingLeft:'20px',paddingRight:'30px'}}>
            <button class="button col-md-12 col-xs-12" onClick={()=>this.renderRedirect("submit")}><span>Go</span></button>
          </div>

        </div>

      </div>
      <div className="col-md-6 col-xs-12" style={{ height: '100vh', width: '100%' }}>
        <Map
        center={{ lat: this.props.fromlat, lng: this.props.fromlng }}
        zoom={8}
        places={places1}
        />
      </div>
    </div>

  </div>
</div>;

res1 = <div className="wrapper">
  <div className="">
    <div className="row">
      <div className="col-md-6 col-xs-12 text-center">

        <div className="card card-success card-outline">
          <div className="card-header">

            <h3 className="card-title text-center">
              <div className="row rowflexdisable">
                <div className="col-md-3 col-xs-6">
                  <button className="btn1 btn-4 btn-4b" style={{float:'left',fontWeight:'500'}} onClick={()=>this.renderRedirect('back')}><i className="fa fa-arrow-left">&nbsp;Back</i></button>
                </div>
                <div className="col-md-6 col-xs-6">
                  <span style={{marginTop:'10px',fontFamily:'fantacy',fontWeight:'500',fontSize:'28px',
                  background: 'linear-gradient(to right, darkviolet 0%, #f4511e 100%)',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',marginLeft:'-10px' }}> Local </span>
                </div>
                <div className="col-md-3 col-xs-6">
                  <button className="btn1 btn-4 btn-4a" style={{float:'right',fontWeight:'500'}} onClick={()=>this.renderRedirect('next')}><i className="fa fa-arrow-right">&nbsp;Next</i></button>
                </div>
              </div>
            </h3>
          </div>

          <div className="card-body">
            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <button type="button" className="btn btn-success" style={{width: "70px"}}>From</button>
              </div>
              <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address }/>
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <button type="button" className="btn btn-danger" style={{width: "70px"}}>To</button>
              </div>
              <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address1 }/>
            </div> 

            <form action="" className="material">

              <div className="form-group">
                <label className="select2text">Name of the Courier</label>
                <select className="form-control" style={{width: '100%'}} placeholder="Courier Name" name="courier" onChange={this.onChange}>
                  <option key="select">Select Courier Name</option>
                  {this.state.couriername.map((team) => <option key={team.value} value={team.value}>{team.display}</option>)}
                </select>
              </div>

              <div className="form-group">
                <label className="select2text">Product</label>
                <select id="prod" className="form-control select2" style={{width: '100%'}} placeholder="Product">
                  <option key="select">Select Product</option>
                  {this.state.product.map((team) => <option key={team.value} value={team.value}>{team.display}</option>)}
                </select>
              </div>

              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="select2text">Document</label>
                    <select className="form-control" style={{width: '100%'}} placeholder="Number of Docs" name="nDocs" onChange={ this.onChange }>
                      <option key="select">Select No. of Docs</option>
                      <option key="1">1</option>
                      <option key="2">2</option>
                    </select>
                  </div>

                </div>

                <div className="col-6">
                  <div className="form-group">
                    <label className="select2text">Box</label>
                    <select className="form-control" style={{width: '100%'}} placeholder="Number of Boxes" name="nBox" onChange={ this.onChange }>
                      <option key="select">Select No. of Boxs</option>
                      <option key="1">1</option>
                      <option key="2">2</option>
                    </select>
                  </div>

                </div>

                {textboxs}
                {textboxs1}

              </div>

              <textarea name="remarks" placeholder="Remarks"></textarea>

            </form>

          </div>
          <div className="row rowflexdisable" style={{paddingLeft:'20px',paddingRight:'30px'}}>
            <button class="button col-md-12 col-xs-12" onClick={()=>this.renderRedirect("submit")}><span>Go</span></button>
          </div>

        </div>

      </div>
      <div className="col-md-6 col-xs-12" style={{ height: '100vh', width: '100%' }}>
        <Map
        center={{ lat: this.props.fromlat, lng: this.props.fromlng }}
        zoom={8}
        places={places1}
        />
      </div>
    </div>

  </div>
</div>;

if(this.state.isSubmit)
{return <Amountcalc 
isType='courier'
fromlat={this.props.fromlat}
fromlng={this.props.fromlng}
tolat={this.props.tolat}
tolng={this.props.tolng}
isLocal={this.props.isLocal}
isDomestic={this.props.isDomestic}
address={this.props.address} 
address1={this.props.address1}
courier={this.state.courier}
DL1={this.state.DL1}
DB1={this.state.DB1}
DL2={this.state.DL2}
DB2={this.state.DB2}
BL1={this.state.BL1}
BB1={this.state.BB1}
BH1={this.state.BH1}
BL2={this.state.BL2}
BB2={this.state.BB2}
BH2={this.state.BH2}
 />
}
else if(isClick1)
return <Category />
else if(this.state.isBack)
{
  return <Map1
  google={this.props.google}
  center={{lat: this.props.fromlat, lng: this.props.fromlat}}
  height='600px'
  zoom={15}
  isType='courier'
  mapHeader='Courier'
  fromlat={this.props.fromlat}
  fromlng={this.props.fromlng}
  tolat={this.props.tolat}
  tolng={this.props.tolng}
  isLocal={this.props.isLocal}
  isDomestic={this.props.isDomestic}
  address={this.props.address} 
  address1={this.props.address1}
  />
}
else if(this.state.isNext)
{
 return <Category />
}
else if(this.props.isLocal) return (res);
else return (res1);
}
}

export default App1;
// background: 'linear-gradient(to right, #30CFD0 0%, #330867 100%)',
//    WebkitBackgroundClip: 'text',
//    WebkitTextFillColor: 'transparent' 